<template>
    <div class="add-product">
        <div class="choose-type">
            选择服务产品类型：
            <Select v-model="productType" @on-change="goToAddProduct">
                <Option v-for="item in serviceType" :key="item.dictionaryKey" :value="item.dictionaryKey">{{item.dictionaryValue}}</Option>
            </Select>
        </div>
        <div class="white-bg">
            <div class="add-site">
                <h6>添加路线场所：</h6>
                <div class="container">
                    <Form
                    ref="formValidate"
                    inline
                    :model="siteData"
                    :rules="ruleValidate"
                    :label-colon="true"
                    label-position="left"
                    :label-width="85">
                        <FormItem label="启运地" prop="dispatchCity" :label-width="70">
                            <adt-city-selector @on-change="changeDispatchCity"></adt-city-selector>
                        </FormItem>
                        <FormItem label="选择场所" prop="dispatchSite">
                            <Select v-model="siteData.dispatchSite" clearable>
                                <Option v-for="(option, index) in dispatchOptions" :value="`${option.value}~${option.label}`" :key="index">{{option.label}}</Option>
                            </Select>
                        </FormItem>
                        <svg class="iconfont arrow" aria-hidden="true">
                            <use xlink:href="#icon-andaotongcaidanICON_jiantou"></use>
                        </svg>
                        <FormItem label="目的地" prop="destinationCity" :label-width="70" style="margin-left: 50px;">
                            <adt-city-selector @on-change="changeDestinationCity"></adt-city-selector>
                        </FormItem>
                       <FormItem label="选择场所" prop="destinationSite">
                            <Select v-model="siteData.destinationSite" clearable>
                                <Option v-for="(option, index) in destinationOptions" :value="`${option.value}~${option.label}`" :key="index">{{option.label}}</Option>
                            </Select>
                        </FormItem>
                    </Form>
                </div>
            </div>
            <div class="choose-parameter">
                <h6>选择参数：</h6>
                <div class="container">
                    <Form
                        ref="parameterFormValidate"
                        inline
                        :model="parameterData"
                        :rules="parameterRuleValidate"
                        :label-colon="true"
                        label-position="left"
                        :label-width="126">
                        <FormItem label="运输方式" prop="trspMode">
                            <Select v-model="parameterData.trspMode" clearable>
                                <Option v-for="item in trspMode" :key="item.dictionaryKey" :value="`${item.dictionaryKey}~${item.dictionaryValue}`">{{item.dictionaryValue}}</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="运输类型" prop="trspType">
                            <Select v-model="parameterData.trspType" clearable>
                                <Option v-for="item in trspType" :key="item.dictionaryKey" :value="`${item.dictionaryKey}~${item.dictionaryValue}`">{{item.dictionaryValue}}</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="物流类型" prop="logisticsType">
                            <Select v-model="parameterData.logisticsType" clearable>
                                <Option v-for="item in logisticsType" :key="item.dictionaryKey" :value="`${item.dictionaryKey}~${item.dictionaryValue}`">{{item.dictionaryValue}}</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="货物类型" prop="cargoType">
                            <Select v-model="parameterData.cargoType" clearable>
                                <Option v-for="item in cargoType" :key="item.dictionaryKey" :value="`${item.dictionaryKey}~${item.dictionaryValue}`">{{item.dictionaryValue}}</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="集装箱尺寸类型" prop="contaSizeType" :class="this.parameterData.trspType && this.parameterData.trspType.split('~')[0] === 'C' ? 'ivu-form-item-required' : ''">
                            <Select v-model="parameterData.contaSizeType" clearable>
                                <Option v-for="item in contaSizeType" :key="item.dictionaryKey" :value="`${item.dictionaryKey}~${item.dictionaryValue}`">{{item.dictionaryValue}}</Option>
                            </Select>
                        </FormItem>
                        <!-- <FormItem label="装箱类型" prop="pckType">
                            <Select v-model="parameterData.pckType" clearable>
                                <Option v-for="item in pckType" :key="item.dictionaryKey" :value="`${item.dictionaryKey}~${item.dictionaryValue}`">{{item.dictionaryValue}}</Option>
                            </Select>
                        </FormItem> -->
                        <FormItem label="集装箱来源" prop="contaSource" :class="this.parameterData.trspType && this.parameterData.trspType.split('~')[0] === 'C' ? 'ivu-form-item-required' : ''">
                            <Select v-model="parameterData.contaSource" clearable>
                                <Option v-for="item in contaSource" :key="item.dictionaryKey" :value="`${item.dictionaryKey}~${item.dictionaryValue}`">{{item.dictionaryValue}}</Option>
                            </Select>
                        </FormItem>
                    </Form>
                    <div class="btns">
                        <Button @click="handleReset()">重置参数</Button>
                        <Button type="primary" @click="saveParameter()">保存参数</Button>
                    </div>
                </div>
            </div>
            <div class="public-component-line bot-10"></div>
            <!-- 一期不做删除、以后要做的时候，记得把./style.scss 165行的代码改一下 -->
            <!-- <adt-button :allDelShow="true" :delUrl="delUrl" :selectList="selectList" v-if="productAllInfo.length"></adt-button> -->
            <div class="product-list public-modify-record">
                <div class="collapse">
                    <Collapse v-model="curCollapse" accordion>
                        <template v-for="(product, index) in productAllInfo">
                            <!-- 一期不做删除、以后要做的时候，记得把./style.scss 165行的代码改一下 -->
                            <!-- <Checkbox v-model="product.checked" @on-change="chooseProduct(product.id, product.checked)" :disabled="index === 0 && hasPendingProduct"></Checkbox> -->
                            <Panel :name="index + ''" @click="curCollapse = index">
                                <Row>
                                    <!-- <Col span="5" class="product-number" v-if="product.serverCode">
                                        产品编号：{{ product.serverCode }}
                                    </Col>
                                    <Col span="9" class="line">
                                        {{ product.sites.dispatchCity }}{{ product.sites.dispatchSite }}
                                        <svg class="iconfont arrow" aria-hidden="true">
                                            <use xlink:href="#icon-andaotongcaidanICON_jiantou"></use>
                                        </svg>
                                        {{ product.sites.destinationCity }} {{ product.sites.destinationSite }}
                                    </Col>
                                    <Col :span="product.serverCode ? 6 : 10" class="parameter">
                                        产品参数：{{ product.parameters }}
                                    </Col>
                                    <Col span="3" class="btns" v-if="index === 0 && hasPendingProduct">
                                        <Button size="small" @click.stop="saveProduct(product)">保存产品</Button>
                                    </Col> -->
                                    <Col span="6" class="product-number" v-if="product.serverCode">
                                        产品编号：{{ product.serverCode }}
                                    </Col>
                                    <Col span="13" class="parameter">
                                        产品参数：{{ product.parameters }}
                                    </Col>
                                    <Col span="4" class="btns" v-if="index === 0 && hasPendingProduct">
                                        <Button size="small" @click.stop="saveProduct(product)">保存产品</Button>
                                    </Col>
                                </Row>
                                <div slot="content">
                                    <div class="product-content">
                                        <div :class="['scene-btns', { spread: isSceneSpread }]">
                                            <adt-tab-btns :titleNames="product.sceneNames" :isSmallBtn="true" :btnIndex="product.sceneTabIndex || -1" :productIndex="index" @handleBtn="handleSceneBtn" class="scene-tab"></adt-tab-btns>
                                            <div class="right-item">
                                                <Button size="small" @click="chooseScene(index)" :disabled="!(index === 0 && hasPendingProduct)">选择场景</Button>
                                                <div class="show-all-scene" @click="isSceneSpread = !isSceneSpread">{{`${!isSceneSpread ? '展开' : '收起'}所有 >`}}</div>
                                            </div>
                                        </div>
                                        <!-- 循环场景，默认选中第一个场景 -->
                                        <!-- <div v-for="(item, idx) in product.sceneNames" :key="idx" v-show="item === curSceneName || (!product.sceneTabIndex && idx === 0)"> -->
                                            <!-- 循环场景，默认不选中场景 -->
                                        <div v-for="(item, idx) in product.sceneNames" :key="idx" v-show="item === curSceneName">
                                            <adt-tab-btns :titleNames="titleNames" :isUnderline="true" :btnIndex="tabIndex" @handleBtn="handleBtn" :width="250" :height="40" class="task-tab"></adt-tab-btns>
                                            <div class="basic-item" v-show="isBasicTask">
                                                <Row class="item-container">
                                                    <Col span="10" class="item">
                                                        基础服务总费用：
                                                        <Input v-model.number.trim="product.scenesList[idx].taskBasesList.taskCost" :disabled="!(index === 0 && hasPendingProduct)" />
                                                        <Select v-model="product.scenesList[idx].taskBasesList.curr" clearable :disabled="!(index === 0 && hasPendingProduct)" style="width: 80px;">
                                                            <Option v-for="item in currencyCode" :key="item.dictionaryKey" :value="item.dictionaryKey">{{item.dictionaryValue}}</Option>
                                                        </Select>/{{ product.trspType === 'C' ? 'TEU' : 'KG' }}
                                                    </Col>
                                                    <Col span="14" class="item">
                                                        基础服务总时长：
                                                        <Input v-model.number.trim="product.scenesList[idx].taskBasesList.serverDuration" :disabled="!(index === 0 && hasPendingProduct)" />
                                                        <Select v-model="product.scenesList[idx].taskBasesList.serverDurationUnit" clearable  :disabled="!(index === 0 && hasPendingProduct)" style="width: 80px;">
                                                            <Option v-for="item in timeUnit" :key="item.dictionaryKey" :value="item.dictionaryKey">{{item.dictionaryValue}}</Option>
                                                        </Select>
                                                    </Col>
                                                </Row>
                                                <adt-task-content :basicTask="product.scenesList[idx].taskBasesList.lists" :isAdditionalTasks="false" :showPriceAndTime="false" />
                                            </div>
                                            <div class="additional-item" v-show="!isBasicTask">
                                                <h6>选择附加任务:</h6>
                                                <div class="task-container">
                                                    <!-- 一行放2个放不下，不能按照设计图来 -->
                                                    <div class="task-item" v-for="(additionalTask, key) in product.scenesList[idx].taskAffixesList" :key="key">
                                                        <Checkbox v-model="additionalTask.isChecked" :disabled="!(index === 0 && hasPendingProduct)"></Checkbox>
                                                        <div class="task-name">{{ additionalTask.taskName }}</div>
                                                        <div class="cost">
                                                            费用：
                                                            <Input v-model.number.trim="additionalTask.taskCost" :disabled="!(index === 0 && hasPendingProduct)" />
                                                            <Select v-model="additionalTask.curr" clearable :disabled="!(index === 0 && hasPendingProduct)" style="width: 80px; margin-left: 12px;">
                                                                <Option v-for="item in currencyCode" :key="item.dictionaryKey" :value="item.dictionaryKey">{{item.dictionaryValue}}</Option>
                                                            </Select>/{{ product.trspType === 'C' ? 'TEU' : 'KG' }}
                                                        </div>
                                                        <div class="time">
                                                            时长：
                                                            <Input v-model.number.trim="additionalTask.serverDuration" :disabled="!(index === 0 && hasPendingProduct)" />
                                                            <Select v-model="additionalTask.serverDurationUnit" clearable :disabled="!(index === 0 && hasPendingProduct)"  style="width: 80px; margin-left: 12px;">
                                                                <Option v-for="item in timeUnit" :key="item.dictionaryKey" :value="item.dictionaryKey">{{item.dictionaryValue}}</Option>
                                                            </Select>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="public-component-line thin-line"></div>
                                        <Row class="set-dead-line">
                                            <Col span="10">
                                                产品起止日期：<DatePicker v-model="product.serverStartTime" type="date" :disabled="!(index === 0 && hasPendingProduct)"></DatePicker> -- <DatePicker v-model="product.serverEndTime" type="date" :disabled="!(index === 0 && hasPendingProduct)"></DatePicker>
                                            </Col>
                                            <Col span="14">
                                                产品报价截止时间前 <Input v-model.number="product.serverRemindDuration" :disabled="!(index === 0 && hasPendingProduct)" /> 天开始提醒
                                            </Col>
                                        </Row>
                                    </div>
                                </div>
                            </Panel>
                        </template>
                    </Collapse>
                </div>
            </div>
        </div>
        <!-- 查看全部场景modal -->
        <adt-my-modal
            title="选择场景"
            :showModal="isSceneModalShow"
            @on-visible-change="sceneVisibleChange"
            @on-ok="confirmChooseScene()"
            width="80%">
            <div class="choose-scene-container">
                <div class="scene-container">
                    <div class="search-container">
                        <div class="search">
                            场景名称：<Input v-model="sceneName"></Input><Icon @click="searchScene" type="ios-search" class="search-btn" />
                        </div>
                        <Button @click="previewSort()" type="primary">排序预览</Button>
                    </div>
                    <Table :columns="sceneColumns" :data="sceneTableData" @on-selection-change="selectionChange" stripe max-height="500">
                        <template #sort="{ row, index }">
                            <div style="position: relative;">
                                <span class="required-tag">*</span><Input v-model.trim="row.sort" @on-change="changeRowData(row, index, 'sort')" clearable></Input>
                            </div>
                        </template>
                    </Table>
                </div>
                <div class="sort-container">
                    <div class="title">场景排序</div>
                    <Table :columns="previewColumns" :data="previewTableData" stripe></Table>
                </div>
            </div>
            <!-- <Form
                ref="sceneFormValidate"
                inline
                :label-colon="true"
                label-position="left">
                <FormItem>
                    <CheckboxGroup v-model="chosenScene" class="scene-checkbox">
                        <Checkbox v-for="(item, index) in 10" :key="index" :label="`${item}~场景`">{{ `场景${index}` }}</Checkbox>
                    </CheckboxGroup>
                </FormItem>
            </Form> -->
        </adt-my-modal>
    </div>
</template>

<script>
    import addProductContent from '@/mixins/addProductContent'
    import AdtTaskContent from '@/components/adt-task-content/Index'
    import validateRules from '@/libs/validateRules'
    import {
        apiTransportAddCode,
        apiTransportAddServer
    } from '@/api/productEditor'
    export default {
        mixins: [addProductContent],
        components: {
            AdtTaskContent
        },
        data () {
            return {
                productType: 'T',
                serviceType: this.$getDictionary('SERVICE_TYPE'),
                trspMode: this.$getDictionary('TRANSPORT_MODE'),
                trspType: this.$getDictionary('TRANSPORT_TYPE'),
                logisticsType: this.$getDictionary('LOGISTICS_TYPE'),
                cargoType: this.$getDictionary('GOODS_TYPE'),
                contaSizeType: this.$getDictionary('CONTAINER_SIZE_TYPE'),
                // pckType: this.$getDictionary('SUITCASE_TYPE'),
                contaSource: this.$getDictionary('CONTAINER_SOURCE'),
                currencyCode: this.$getDictionary('CURRENCY_CODE'),
                timeUnit: this.$getDictionary('TIME_UNIT'),
                siteData: {
                    dispatchCity: '',
                    dispatchSite: '',
                    destinationCity: '',
                    destinationSite: ''
                },
                ruleValidate: {
                    dispatchCity: [
                        validateRules.select('启运地')[0]
                    ],
                    // dispatchSite: [
                    //     validateRules.select('启运地场所')[0]
                    // ],
                    destinationCity: [
                        validateRules.select('目的地')[0]
                    ],
                    // destinationSite: [
                    //     validateRules.select('目的地场所')[0]
                    // ]
                },
                parameterData: {
                    trspMode: '',
                    trspType: '',
                    logisticsType: '',
                    cargoType: '',
                    contaSizeType: '',
                    // pckType: '',
                    contaSource: ''
                },
                parameterRuleValidate: {
                    trspMode: [
                        validateRules.select('运输方式')[0]
                    ],
                    trspType: [
                        validateRules.select('运输类型')[0]
                    ],
                    logisticsType: [
                        validateRules.select('物流类型')[0]
                    ],
                    cargoType: [
                        validateRules.select('货物类型')[0]
                    ]
                },
                dispatchOptions: [],
                destinationOptions: [],
                delUrl: function () {}
            }
        },
        methods: {
            // 保存产品
            saveProduct (productData) {
                let product = JSON.parse(JSON.stringify(productData))
                // 至少选择一个场景
                if (!product.sceneNames.length) {
                    this.$Message.warning('请先选择场景！')
                    return
                }
                product.serverStartTime = +new Date(product.serverStartTime) || ''
                product.serverEndTime = +new Date(product.serverEndTime) || ''
                // 产品起止日期必填
                if (!product.serverStartTime || !product.serverEndTime) {
                    this.$Message.warning('请输入产品起止日期！')
                    return
                }
                // 起始时间不能大于截止时间
                if (product.serverStartTime > product.serverEndTime) {
                    this.$Message.warning('起始时间不能大于截止时间！')
                    return
                }
                // 产品报价截止时间
                if (!product.serverRemindDuration) {
                    this.$Message.warning('请输入产品报价截止时间')
                    return
                }
                for (let scene of product.scenesList) {
                    // 基础任务必填校验
                    if (!scene.taskBasesList.taskCost) {
                        this.$Message.warning(`请输入${scene.sceneName}下的基础服务总费用！`)
                        return
                    }
                    if (!scene.taskBasesList.curr) {
                        this.$Message.warning(`请输入${scene.sceneName}下的基础服务总费用单位！`)
                        return
                    }
                    if (!scene.taskBasesList.serverDuration) {
                        this.$Message.warning(`请输入${scene.sceneName}下的基础服务总时长！`)
                        return
                    }
                    if (!scene.taskBasesList.serverDurationUnit) {
                        this.$Message.warning(`请输入${scene.sceneName}下的基础服务总时长单位！`)
                        return
                    }
                    // 基础任务数据结构组装
                    for (const list of scene.taskBasesList.lists) {
                        list.taskCost = scene.taskBasesList.taskCost
                        list.curr = scene.taskBasesList.curr
                        list.serverDuration = scene.taskBasesList.serverDuration
                        list.serverDurationUnit = scene.taskBasesList.serverDurationUnit
                    }
                    scene.taskBasesList = scene.taskBasesList.lists
                    // 附加任务
                    for (let key = 0; key < scene.taskAffixesList.length; key++) {
                        // 选中的附加任务
                        if (scene.taskAffixesList[key].isChecked) {
                            if (!scene.taskAffixesList[key].taskCost) {
                                this.$Message.warning(`请输入${scene.sceneName}选中的附加任务的费用！`)
                                return
                            }
                            if (!scene.taskAffixesList[key].curr) {
                                this.$Message.warning(`请输入${scene.sceneName}选中的附加任务的费用单位！`)
                                return
                            }
                            if (!scene.taskAffixesList[key].serverDuration) {
                                this.$Message.warning(`请输入${scene.sceneName}选中的附加任务的时长！`)
                                return
                            }
                            if (!scene.taskAffixesList[key].serverDurationUnit) {
                                this.$Message.warning(`请输入${scene.sceneName}选中的附加任务的时长单位！`)
                                return
                            }
                            delete scene.taskAffixesList[key].isChecked
                        } else {
                            scene.taskAffixesList.splice(key, 1)
                            key--
                        }
                    }
                }
                // 将数据结构处理成后端一摸一样的结构
                delete product.parameters
                delete product.sceneNames
                delete product.sceneTabIndex
                delete product.sites
                // 保存成功
                apiTransportAddServer({ data: product }).then(res => {
                    this.$Message.success(res.message)
                    this.hasPendingProduct = false
                })
            },
            // 选择启运地城市
            async changeDispatchCity (value) {
                // this.siteData.dispatchCity = `${value.data[value.data.length - 1].addressCode}~${value.data[value.data.length - 1].addressName}`
                this.siteData.dispatchCity = `${value.value.join(';')}~${value.data[value.data.length - 1].addressName}`
                this.dispatchOptions = await this._handelDir(value.value)
            },
            // 选择目的地城市
            async changeDestinationCity (value) {
                // this.siteData.destinationCity = `${value.data[value.data.length - 1].addressCode}~${value.data[value.data.length - 1].addressName}`
                this.siteData.destinationCity = `${value.value.join(';')}~${value.data[value.data.length - 1].addressName}`
                this.destinationOptions = await this._handelDir(value.value)
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import './style.scss';
</style>
